.images {
  max-width: 650px;
  display: flex;
  width: 100%;
  gap: 20px;
  margin-top: 50px;

  .topImg {
    width: 100%;
    > img {
      border-radius: 10px;
      height: 475px;
      box-shadow: 0px 0px 10px rgba(0, 151, 178, 0.3);
    }
  }

  .bottomImg {
    width: 100%;
    display: flex;
    align-items: end;
    > img {
      border-radius: 10px;
      height: 475px;
      box-shadow: 0px 0px 10px rgba(0, 151, 178, 0.3);
    }
  }
}

@media (max-width: 768px) {
  .images {
    padding: 0px 20px;
  }

  .topImg > img,
  .bottomImg > img {
    height: 100% !important;
  }
}
